html {
  box-sizing: border-box;
  overflow-x: hidden
}
*, *:before, *:after {
  box-sizing: inherit;
}
body{
  font-family: Calibri, Tahoma, sans-serif;
  font-size: 16px;
  color: @col_font;
}
img{
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
figure,
input,
button{
  list-style: none;
  margin: 0;
  padding: 0;
}
// link
a{
  text-decoration: none;
  font-style: normal;
  color: inherit;
}
a:hover{
  cursor: pointer;
}
// input
input:active,
input:focus,
input:hover{
  outline: 0;
  outline-offset: 0;
}
input{
  border: none;
}
// button
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;

  background: transparent;

  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;

  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;

  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;

  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
}
button:active,
button:focus,
button:hover{
  outline: 0;
  outline-offset: 0;
}
button:hover{
  cursor: pointer;
}
address{
  font-style: normal;
}
picture{
  flex-shrink: 0;
}
// h1
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}