.rules {
  text-align: center;
  padding-bottom: 40px;

  .sm-block({
    padding-bottom: 20px;
  });

  &-title {
    .h3();
    padding-bottom: 27px;

    .md-block({
      padding-bottom: 20px;
    })
  }

  &-list {
    .f20();
    .size(11);
    margin: 0 auto;

    .sm-block({
      .f18();
    })
  }
}
.rules .rules-title{
  .xs-block({
    padding-bottom: 10px;
  })
}
.rules-list{
  & ul{
    text-align: start;
  }
  &__item{
    padding-bottom: 10px;
    position: relative;
  }
  &__item::before{
    content: '';
    position: absolute;
    top: 25%;
    left: -20px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: @col_b-red;
  }
}