.about{
  text-align: center;
  padding-top: 71px;
  padding-bottom: 53px;

  .lg-block({
    padding-top: 55px;
    padding-bottom: 40px;
  });
  .md-block({
    padding-top: 30px;
    padding-bottom: 30px;
  });

  &-title{
    .h3();
    padding-bottom: 27px;

    .lg-block({
      padding-bottom: 18px;
    });
    .md-block({
      padding-bottom: 10px;
    })
  }
  &-text{
    .f20();

    .sm-block({
      .f18();
    })
  }
}
.about-prize{
  padding-top: 20px;

  .md-block({
    padding-top: 10px;
  });

  &__counter{
    display: flex;
    justify-content: center;
    padding: 34px 0;
    background-color: @col_back_count;

    .lg-block({
      padding: 28px 0;
    });
    .md-block({
      padding: 16px 0;
    })
  }
}


.counter-item{
  display: flex;
  flex-direction: column;
  align-items: center;

  &__text{
    .f28();
    margin-bottom: 18px;

    .sm-block({
      .f24();
      margin-bottom: 10px;
    });
    .xs-block({
      .f20();
    })
  }
  &__value{
    position: relative;
    padding: 22px 15px 21px 16px;

    .sm-block({
      padding: 0 16px;
    });
    .xs-block({
      padding: 0 12px;
    })
  }
}
.counter-item:not(:last-child){
  margin-right: 50px;

  .md-block({
    margin-right: 30px;
  });

  .counter-item__value::before,
  .counter-item__value::after{
    content: '';
    position: absolute;
    right: -33px;
    width: 11px;
    height: 10px;
    background-color: rgb(190, 10, 71);

    .md-block({
      right: -23px;

      .xs-block({
        right: -14px;
      })
    });
    .sm-block({
      width: 10px;
      height: 9px;
    });
    .xs-block({
      width: 8px;
      height: 7px;
    })
  }
  .counter-item__value::before{
    bottom: 45px;

    .sm-block({
      bottom: 40px;
    });
    .xs-block({
      bottom: 35px;
    })
  }
  .counter-item__value::after{
    top: 45px;

    .sm-block({
      top: 40px;
    });
    .xs-block({
      top: 35px;
    })
  }
}
.about-prize__counter .counter-item:not(:last-child) .counter-item__value::before,
.about-prize__counter .counter-item:not(:last-child) .counter-item__value::after{
  .xs-block({
      right: -14px;
  })
}
.about-prize__counter .counter-item:not(:last-child){
  .xs-block({
    margin-right: 15px;
  });
}
.item-field{
  position: relative;
  .font_RobS-B();
  .f80();
  color: @col_white;
  border: 2px solid @col_black;
  border-radius: 2px;
  background-image: linear-gradient( 90deg, rgb(208,16,83) 0%, rgb(170,4,59) 100%);

  .sm-block({
    .f60();
  });
  .xs-block({
    .f50();
  })
}
.item-field::before,
.item-field::after{
  position: absolute;
  content: '';
  top: 32px;
  width: 3px;
  height: 29px;
  background-color: @col_white-gr;

  .sm-block({
    top: 22px;
    height: 24px;
  });
  .xs-block({
    top: 17px;
  })
}
.item-field::before{
  left: 4px;
}
.item-field::after{
  right: 4px;
}
