.promotion {
  text-align: center;
  padding: 36px 0;
  background-color: @col_back_count;

  .md-block({
    padding: 20px 0;
  });

  &-title {
    .h3();
    padding-bottom: 27px;

    .md-block({
      padding-bottom: 20px;
    })
  }

  &-text {
    .f20();
    margin: 0 auto;
    .size(9);

    .md-block({
      .size(11);
    });
    .sm-block({
      .f18();
    })
  }
}
.promotion .promotion-title{
  .xs-block({
    padding-bottom: 10px;
  })
}