.header,
.wrapper,
.about,
.rules,
.promotion,
.history,
.footer{
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 65px;
  padding-right: 99px;

  .lg-block({
    padding-left: 30px;
    padding-right: 40px;
  });
  .sm-block({
    padding-left: 20px;
    padding-right: 20px;
  });
  .xs-block({
    padding-left: 10px;
    padding-right: 10px;
  })
}
.header-top{
  margin-left: -35px;
  margin-right: -70px;

  .lg-block({
    margin-left: -10px;
    margin-right: -30px;
  });
  .xs-block({
    margin-left: 0;
    margin-right: 0;
  })
}
.header-main,
.slider__content-top{
  margin-left: -65px;
  margin-right: -99px;

  .lg-block({
    margin-left: -30px;
    margin-right: -45px;
  });
  .sm-block({
    margin-left: -20px;
    margin-right: -30px;
  })
}

/* container */
.header-top,
.header-main,
.slider__content-top,
.about-prize__counter,
.promotion,
.history__form,
.footer{
  position: relative;
}

.header-top::after,
.header-main::after,
.slider__content-top::after,
.about-prize__counter::after,
.promotion::after,
.history__form::after,
.footer::after{
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}