.header{

}
.header-top{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 15px;

  .lg-block({
    justify-content: center;
  });

  &__logo{
    margin-right: 38px;
  }
  &__menu ul,
  &__content{
    display: flex;
  }
  &__menu ul{
    .sm-block({
      display: none;
      position: absolute;
      top: 60px;
      right: 10px;
      background-color: @col_white-grV2;
      padding: 10px 20px;
      border-radius: 15px;
      z-index: 22;
    })
  }
  &__content{
    align-items: center;
    margin-left: auto;

    .lg-block({
      margin-left: 0;
      justify-content: center;
      padding-top: 15px;
    });
    .sm-block({
      width: 100%;
      position: relative;
    })
  }
}
.menu-item{
  .f18-5();

  .sm-block({
    padding-bottom: 10px;
  })
}
.menu-item:not(:last-child){
  margin-right: 30px;
}
.menu-item:first-child{
  margin-right: 26px;
}
.menu-item:hover,
.menu-item:active{
  color: @col_text-slide;

  .sm-block({
    color: @col_b-red;
  })
}
// социалки
.social-link:not(:last-child){
  margin-right: 16px;
}
.content__social{
  .xs-block({
    display: none;
  })
}
// ----------
.content__lang{
  .xs-block({
    position: absolute;
    top: -70px;
    right: 0;
  })
}
// кнопка
.content__button{
  position: relative;
  text-align: center;
  color: @col_white;
  .f18-5();
  background-color: @col_orange;
  box-shadow: 2px 9px 17px -5px rgba(251,139,36,1);
  border-radius: 20px;
  margin: 0 51px;
  padding-top: 10px;
  padding-bottom: 9px;
}
.content__button:hover{
  box-shadow: 1px 9px 60px 11px rgba(251,139,36,1);
}
.button-link{
  padding: 10px 31px 9px 30px;
}
.content__button::before,
.content__button::after{
  content: '';
  position: absolute;
  top: 5px;
  width: 1px;
  height: 32px;
  background-color: @col_white-grV2;
}
.content__button::before{
  left: -24px;
}
.content__button::after{
  right: -24px;
}
// -----------
// бургер
.burger-btn{
  display: none;

  .sm-block({
    position: relative;
    display: flex;
    margin-left: 30%;
  })
}
.burger-btn:hover{
  cursor: pointer;
  .icon{
    fill: @col_b-red;
  }
}
.burger-btn:active .icon:active{
  fill: @col_b-red;
}
// burger show
.view{
  display: block !important;
  transition: 3s;
}
///////////////////////////
///////////////////////////
.header-main{
  .xs-block({
    display: none;
  })
}
// С Л А Й Д Е Р
.slider__content{
  position: relative;

  &-top{
    display: flex;
    justify-content: center;
    background-color: rgba(246, 232, 236, 0.2);
    padding: 15px 0;

    .lg-block({
      padding: 10px 0;
    });
    .md-block({
      text-align: center;
      padding: 5px 0;
    })
  }
  &-title{
    .f36();
    padding-top: 74px;
    padding-bottom: 50px;
    margin-left: 90px;

    .xlg-block({
      padding-top: 50px;
      padding-bottom: 40px;
    })
  }
  &-social{
    display: flex;
    flex-direction: column;
    margin-left: 90px;

    .lg-block({
      margin-left: 70px;
    });
    .md-block({
      margin-left: 40px;
    })
  }
}
.header-main .slider__content-social{
  .sm-block({
    margin-left: 20px;
  })
}
.content-social{

  &__text{
    .f18-5();
    padding-bottom: 19px;

    .lg-block({
      padding-bottom: 10px;
    });
    .md-block({
      .f17-5();
    })
  }
  &__items{
    display: flex;
  }
}

.wrapper{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.content-top__item:not(:last-child){
  margin-right: 48px;

  .md-block({
    margin-right: 26px;
  })
}
.header-main .content-top__item:not(:last-child){
  .sm-block({
    margin-right: 0;
  })
}
.content-top__item-text{
  color: @col_text-slide;
}
// слайд с изб.
.slide-image{
  z-index: -1;

  .lg-block({
    min-height: 250px;
    object-fit: cover;
  });
  .sm-block({
    min-height: 200px;
  })
}

.header-main__slider .slider__content-title{
  .lg-block({
    padding-top: 35px;
    padding-bottom: 30px;
    margin-left: 70px;
  });
  .md-block({
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: 40px;
    .f30();
  })
}
.header-main .header-main__slider .slider__content-title{
  .sm-block({
    padding-top: 15px;
    padding-bottom: 5px;
    margin-left: 20px;
    .f28();
  })
}