.history {
  text-align: center;
  padding: 50px 0;

  .lg-block({
    padding: 38px 0;
  });
  .md-block({
    padding: 25px 0;
  });

  &-title {
    .h3();
    padding-bottom: 50px;

    .lg-block({
      padding: 30px 0;
    });
    .md-block({
      padding: 18px 0;
    })
  }
  &-items{
    .f24();
    .size(4.7);
    margin: 0 auto;
    padding-bottom: 50px;

    .lg-block({
      .f22();
      .size(7);
      padding-bottom: 35px;
    });
    .md-block({
      .size(9);
      padding-bottom: 28px;
    })
  }
  // form
  &__form{
    padding: 35px 0;
    background-color: @col_back_count;

    .md-block({
      padding: 25px 0;
    })
  }
}
.history .history__form{
  .sm-block({
    padding: 18px 0;
  })
}
.history .history-items{
  .sm-block({
    .f18-5();
    .size(11);
    padding-bottom: 20px;
   });
  .xs-block({
    .size(12);
  })
}
.history-items__item{
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
}
.history-items__item:nth-child(odd){
  background-color: @col_back_count;
}
.history-items__item::after{
  position: absolute;
  content: '';
  top: 0;
  left: 50%;
  height: 100%;
  width: 1px;
  border: 1px dashed @col_back_dash;
  background-color: @col_back_count;
}
.item-date{
  margin-left: 20px;

  .xs-block({
    margin-left: 16px;
  })
}
.item-value{
  margin-right: 20px;

  .xs-block({
    margin-right: 16px;
  })
}
////////////////
  /* form */
.form{
  position: relative;
  display: flex;
  justify-content: center;
  width: 480px;
  margin: 0 auto;

  .sm-block({
    width: 360px;
  });
  .xs-block({
    width: 260px;
  });

  &-title{
    .f18-5();
    font-weight: bold;
    padding-bottom: 25px;

    .sm-block({
      .f17-5();
      padding-bottom: 18px;
    })
  }
  &-email{
    padding: 18px 269px 15px 50px;
    border: 2px solid @col_text-slide;
    border-radius: 30px;
    background-color: @col_white;

    .sm-block({
      padding: 16px 190px 13px 30px;;
    });
    .xs-block({
      padding: 14px 110px 11px 20px;;
    })
  }
  &-img{
    position: absolute;
    top: 18px;
    left: 18px;

    .sm-block({
      top: 16px;
      left: -5px;
    });
    .xs-block({
      display: none;
    })
  }
  &-btn{
    position: absolute;
    right: -1px;
    padding: 17.5px 0;
    background-color: @col_btn-red;
    color: @col_white;
    border-radius: 30px;
    width: 160px;

    .sm-block({
      padding: 16px;
      right: -12px;
      width: 130px;
    });
    .xs-block({
      padding: 14px;
      right: -15px;
      width: 90px;
      border-radius: 26px;
    })
  }
  &-text{
    padding-top: 13px;
    color: @col_text-slide;

    .sm-block({
      padding-top: 9px;
      margin: 0 10px;
    })
  }
}