.footer{
  background-color: @col_back;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 45px;
  .f18-5();
  color: @col_font;

  .lg-block({
    .f16();
    padding-top: 28px;
    padding-bottom: 32px;
  });
  .md-block({
    justify-content: space-between;
  });
  .sm-block({
    padding-top: 18px;
    padding-bottom: 20px;
  });

  &-menu ul{
    display: flex;
  }
}
.footer-menu{
  .md-block({
    display: flex;
    justify-content: center;
    width: 100%;
    padding-bottom: 20px;
  })
}
.footer .footer-menu{
  .sm-block({
    display: none;
  })
}
.menu-item:not(:last-child){
  margin-right: 30px;

  .lg-block({
    margin-right: 20px;
  })
}
.menu-item:first-child{
  margin-right: 26px;

  .lg-block({
    margin-right: 20px;
  })
}
.footer-social{
  .xs-block({
    margin-left: auto;
    padding-top: 10px;
  })
}