.header-top,
.header-main,
.slider__content-top,
.about-prize__counter,
.promotion,
.history__form,
.footer {
  position: relative;
}
.header-top::after,
.header-main::after,
.slider__content-top::after,
.about-prize__counter::after,
.promotion::after,
.history__form::after,
.footer::after {
  content: '';
