.slick-dotted.slick-slider {
  margin-bottom: 0;
}
.slick-prev,
.slick-next {
  top: 50%;
  padding: 16px;
  background-color: rgba(0, 0, 0, 0.047);
}
.slick-prev {
  left: 0;
  z-index: 1;
  border-radius: 0 6px 6px 0;
}
.slick-next {
  right: 0;
  border-radius: 6px 0 0 6px;
}
.slick-prev:before,
.slick-next:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  border: 1px solid #263238;
  opacity: 0.5;
  width: 10px;
  height: 10px;
  transform: translateY(-50%) rotate(45deg);
}
.slick-prev::before {
  left: 15px;
  border-right: 0;
  border-top: 0;
}
.slick-next::before {
  right: 15px;
  border-left: 1px;
  border-bottom: 1px;
}
.slick-active {
  outline: none;
}
.slick-dots {
  position: absolute;
  display: flex;
  bottom: 5px;
  left: 45%;
  width: 10%;
}
@media screen and (max-width: 992px) {
  .slick-dots {
    bottom: -5px;
  }
}
.slick-dots li {
  margin-right: 10px;
}
.slick-dots li button:before {
  content: url("../images/dott.png");
}
.slick-dots li.slick-active button:before {
  content: url("../images/dott-act.png");
  opacity: 1;
}
@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }
  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
@keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }
  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
.pulse {
  -webkit-animation-name: pulse;
  animation-name: pulse;
  animation-duration: 2s;
}
